class Tab extends Component{
  constructor(props){
    super(props);
    this.state = {isCurrent: false}
  }
    
  render(){
    return (
      <li className={this.props.isCurrent ? 'current-tab' : null}>
        <a onClick={this.props.handleClick} href='localhost:3000'>
        {this.props.name}
        </a>
      </li>
    );
  }
}

class Tabs extends Component{
    handleClick(tab){
    	this.props.changeTab(tab);
    }
    
    render(){
        return (
            <nav>
                <ul>
                {this.props.tabList.map(function(tab) {
                    return (
                        <Tab
                            handleClick={this.handleClick.bind(this, tab)}
                            key={tab.id}
                            url={tab.url}
                            name={tab.name}
                            isCurrent={(this.props.currentTab === tab.id)}
                         />
                    );
                }.bind(this))}
                </ul>
            </nav>
        );
    }
}

class ProfileImg extends Component {
  constructor(props){
    super(props);
    this.toggleImg = this.toggleImg.bind(this);
    this.state = {showprofpic: true};
  }
  toggleImg(){
    this.setState({showprofpic: !this.state.showprofpic });
  }
  render() {
    return (
      <div className="about-img" ><img src={this.state.showprofpic ? profileimage2 : profileimage1} onClick={this.toggleImg} alt="me!"/><div className="caption"><small>{this.state.showprofpic ? "click the image for a cooler one!" : null}</small></div></div>
    );
  }
}


