class App extends Component{
  constructor(props) {
    super(props);
    this.state = {
      tabList: tabList,
      projectList: projectList,
      currentTab: 0
    }
  }
  changeTab(tab){
    this.setState({ currentTab: tab.id });
    setConfiguration({ defaultScreenClass: 'sm', gridColumns: 12, gutterWidth: tab.id === 0 ? 30 : 60 });
  }
  render(){
    return(
      <div className='App'>
        <div className='Header'>        
          <HeaderName />
          <Tabs
            currentTab={this.state.currentTab}
            tabList={this.state.tabList}
            changeTab={this.changeTab.bind(this)}
          />
        </div>
        <Content 
          currentTab={this.state.currentTab} 
          projectList={this.state.projectList}
        />
      </div>
    );
  }
}

export default App;
